function bindleftCommonSearchdropdown(instance, moduleName, filtername) {
    
    var vm = instance;
    var param = "moduleName=DocStorage&type=DEPARTMENT"
    var param1 = "moduleName=DocStorage&type=HR_DESIG"
    if (filtername == 'DEPARTMENT') {
        DataService.GetDDLDatas(param).then((response) => {
            
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchfeild[0].listOptions.push(obj);
            });
            return vm.$parent.DepartmentDataTemp = response.data.DATA;
        });
    }
    else{
        DataService.GetDDLDatas(param1).then((response) => {
            
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchfeild[1].listOptions.push(obj);
            });
            return vm.$parent.DepartmentDataTemp = response.data.DATA;
        });
    }
};
export default { lefrsearchschema, bindleftCommonSearchdropdown }
