<template>
   
    <section id="main-content" class="main-content">
        <loader :is-visible="isLoading"></loader>
        <!-- start page heading -->
        <section class="main-content">
            <div class="container-fluid padding-t_10">
                <!-- start left panel -->
                <div class="left-menu-tab " id="UserLeftMenu">
                    <leftsearch @SetLeftSearch="SetLeftSearch" @click="ResetButton" classs="DepartmentchkItems" header="Manage User Storage" v-bind:fields="leftSearchfeild" modulename="DocStorage"></leftsearch>
                </div>
              
                <div class="right-content" id="UserRightContent">
                    <div class="col-md-12 pb-0">
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                <StorageBar :type="'Doc'"></StorageBar>
                            </div>
                        </div>
                    </div>
                    <hr class="mt-2 mb-2 p-0" />
                    <div class="col-md-12 p-0" v-for="(item,index1) in userStorageDetailSummary" :key="index1">
                        <!--dashboard-->
                        <ul class="dashbord-widget">
                           <li class="one_wid">
                                <h3><span id="spnStorageLimit" class="inherit-span">{{
                                    item.DOC_STORAGE_MAXLIMIT
                                }}</span> MB<br><span>Total</span></h3>
                                <em class="fa fa-hdd-o" aria-hidden="true"></em>
                            </li>
                            <li class="two_wid">
                                <h3><span id="TotalConsumedSpace" class="inherit-span">{{
                                   totalConsumedStorage
                                }}</span>MB<br><span>Consumed</span></h3>
                                <em class="fa fa-hdd-o" aria-hidden="true"></em>
                            </li>
                            <li class="three_wid">
                                <h3><span id="TotalAvailableSpace" class="inherit-span">{{
                                   item.DOC_STORAGE_MAXLIMIT-(AllowedClientStorage + totalAllowedStorage)
                                }}</span>MB<br><span>Available</span></h3>
                                <em class="fa fa-hdd-o" aria-hidden="true"></em>
                            </li>
                            <li class="four_wid">
                                <h3><span id="AssignedtoUser" class="inherit-span">{{ totalAllowedStorage }}</span>
                                    MB<br><span>Assigned To User</span></h3>
                                <em class="fa fa-user-o" aria-hidden="true"></em>
                            </li>
                            <li class="five_wid">
                                <h3><span class="inherit-span" id="abc">{{ AllowedClientStorage }}</span>
                                    MB<br><span>Assigned To Client</span>
                                </h3>
                                <em class="fa fa-id-card-o" aria-hidden="true"></em>
                            </li>
                        </ul>
                        <!--dashboard-->
                        <div class="clearfix"></div>
                    </div>
                    <div class="col-md-12 p-0">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-actions float-right">
                                <a id="btnStorageSpace" class="p-action-btn text-white disabled" href="#myModal"
                                    @click="AllocatedSpacePopup" data-toggle="modal" alt="Allocate Space"
                                    title="Allocate Space">
                                    <i class="fa fa-plus"></i><br />Allocate Space
                                </a>
                                <!-- <RouterLink to="/AdvancedSetup/Detail" class="p-action-btn text-white" href="#myModal"
                                    data-toggle="modal" alt="Allocate Space"
                                    title="Allocate Space">
                                    <i class="fa fa-angle-double-left"></i><br />Back    
                                </RouterLink> -->
                                <input id="ReportViewer" type="hidden" value="L" />
                            </span>
                            <span class="collapse-head-btns">
                                <a href="javascript:;" class="collapse-btns"><i
                                        class="fa fa-angle-up text-white"></i></a>
                            </span>
                        </div>
                    </div>
                    <div class="col-md-12 p-0">
                        <div class="listing mt-2">
                            <div class="expand-list">
                                <a href="javascript:;" @click="ExpandCollapseList"
                                    class="expand-list-btn justify-content-center align-items-center">
                                    <em class="icon-solid arrow-solid-left"></em>
                                </a>
                            </div>

                            <!-- Listing record of user storage -->
                            <div class="listing">
                                <div class="table-responsive table-fix-header" id="div_forListView">
                                    <table class="table table-bordered mt-0" style="width: 100%;" id="tablelistingdata">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th style="width:50px !important;" class="text-center"><input
                                                        id="chkAll" class="chkitems" value="" type="checkbox"
                                                        v-on:change="onChange()">
                                                </th>
                                                <th>
                                                    <span>User Name</span>
                                                </th>
                                                <th>
                                                    <span>Designation</span>
                                                </th>
                                                <th>
                                                    <span>Department</span>
                                                </th>
                                                <th>
                                                    <span>Status</span>
                                                </th>
                                                <th>
                                                    <span>Consumed Storage(MB)</span>
                                                </th>
                                                <th>
                                                    <span>Allocated Storage (MB)</span>
                                                </th>
                                                <th data-width="5%" class="text-center nonresize">
                                                    <span>Action</span>
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item,index) in UserData" :key="index">
                                                <td class="text-center"><input :Id="item.USER_ID"
                                                        v-bind:value="item.USER_ID" class="chkItems" type="checkbox"
                                                        v-on:change="onChange()"></td>
                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        {{ item.USER_NAME }}
                                                    </span>
                                                </td>
                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        {{ item.DESIGNATION_NAME }}
                                                    </span>
                                                </td>
                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        {{ item.DEPARTMENT_NAME }}
                                                    </span>
                                                </td>
                                                <td>
                                                    <a class="inactive-disabled">
                                                    <span
                                                        class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-success"
                                                        v-if="item.STATUS_ID == '1001'"> Active</span>

                                                    <span title=""
                                                        class="sent bg-secondary btn d-inline-block w-80 text-center text-truncate text-white"
                                                        v-if="item.STATUS_ID == '1084'"> Approved</span>

                                                    <span title=""
                                                        class="stop btn d-inline-block w-80 text-center text-truncate btn-danger"
                                                        v-if="item.STATUS_ID == 'Rejected'"> Rejected</span>

                                                    <span title=""
                                                        class="stop px-2 bg-primary btn d-inline-block w-80 text-center text-truncate"
                                                        v-if="item.STATUS_ID == '1003'"> Deleted</span>

                                                    <span
                                                        class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-danger"
                                                        v-if="item.STATUS_ID == '1002'">Inactive</span>

                                                    <span
                                                        class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-secondary"
                                                        v-if="item.STATUS_ID == '1005'"> Pending</span>

                                                    <span
                                                        class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-warning"
                                                        v-if="item.STATUS_ID == 'ActivationAwaited'">
                                                        ActivationAwaited</span>
                                                        </a>
                                                </td>
                                                <td>
                                                    <span class="text-right text-overflow-dynamic-container">
                                                        {{item.USED_STORAGE }}
                                                    </span>
                                                </td>
                                                <td>
                                                    <span class="text-overflow-dynamic-container" v-if="item.STATUS_ID!='1002'">
                                                        <input type="text" @keypress="isNumber($event)"
                                                            :id="'idMaxStorage' + item.USER_ID" step="0.01"
                                                            maxlength="13" v-model="item.ALLOWED_STORAGE" disabled="disabled"
                                                            class="maxAllowedStorage  form-control text-right" />
                                                        <!-- :value="item.ALLOWED_STORAGE" -->
                                                    </span>
                                                    <span class="text-overflow-dynamic-container" v-if="item.STATUS_ID=='1002'">
                                                        <input type="text" @keypress="isNumber($event)"
                                                            :id="'idMaxStorage' + item.USER_ID" step="0.01" disabled="disabled"
                                                            maxlength="13" value="0"
                                                            class="maxAllowedStorage  form-control text-right" />
                                                        <!-- :value="item.ALLOWED_STORAGE" -->
                                                    </span>
                                                </td>
                                                <td class="text-center single-action">

                                                    <a class="aEdit" title="Edit" v-if="hideEditIcon"
                                                        :id="'idEdit' + item.USER_ID"
                                                        @click="editAllowedStorage(item.USER_ID)"><em
                                                            class="fa fa-pencil text-success action-icon "></em></a>
                                                    <div :id="'divAction' + item.USER_ID" style="display: none;">

                                                        <a class="aCancel mr-2" :id="'idCancel_' + item.USER_ID"
                                                            :value="item.USER_ID" title="Cancel"
                                                            @click="CancelAction(item.USER_ID, item.ALLOWED_STORAGE)"
                                                            href="javascript:;"><em
                                                                class="fa fa-times-circle text-danger action-icon"></em></a>
                                                        <a class="aSave" title="Save" :value="item.USER_ID"
                                                            :id="'idSave_' + item.USER_ID" href="javascript:;"
                                                            @click="SaveData(item.USER_ID, item.ALLOWED_STORAGE)"><em
                                                                class="fa fa-save text-primary action-icon"></em></a>
                                                    </div>
                                                </td>
                                            </tr>
                                            <tr v-if="UserData.length == 0">
                                                <td colspan="7" class="text-danger text-center no-record">{{$t('NoRecordfound')}}</td>
                                            </tr>
                                            <tr v-if="NoRecordLength > 0">
                                                <td colspan="2" class="text-right fr font-weight-bold">Total</td>
                                                <td colspan="4" class="text-right fr font-weight-bold">{{
                                                    totalConsumedStorage
                                                }} MB </td>
                                                <td class=" text-right fr font-weight-bold">{{ totalAllowedStorage }} MB
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <AllocatedSpace :jsonobj="jsonobj" v-if="isAllocatedSpaceOpen"></AllocatedSpace>
    </section>

  

</template>

<script>
import Pager from '../../components/Common/Pager.vue';
import UserStorageListingTable from '../../components/Common/UserStorageListingTable.vue';
//import DisplayChart from '../../components/DisplayChart.vue';
import DataService from '../../services/DataService';
import AllocatedSpace from '../UI/Modal/ModalAllocatedSpace.vue'
import StorageBar from '../../components/Common/StorageBar.vue'
import leftsearch from "../../components/Common/left-search.vue"
import schema from "./Modal/UserStorageLeftSearchSchema"
export default {
    components: {
    Pager,
    AllocatedSpace,
    UserStorageListingTable,
    StorageBar,
    leftsearch
},

    data() {
        return {

            isLoading: false,
            UserData: [],
            DepartmentData: [],
            designationData: [],
            userStorageDetailSummary: [],
            pageNo: 1,
            searchCondition: null,
            sortExp: null,
            sortBy: null,
            pageSize: 10,
            totalRecords: '',
            dropDivShow: false,
            showByNameDiv: false,
            showByConsumedDiv: false,
            divDesignationShow: false,
            hideEditIcon: true,
            isAllocatedSpaceOpen: false,
            UserStorageValue: null,
            UserId: null,
            saveStorage: null,
            StorageValue: null,
            jsonobj: [],
            JSONSearchData: [],
            departmentList: null,
            designationList: null,
            txtSearchUserName: "",
            txtConsumedStorage: "",
            userFilterData: [],
            CheckIfExistDepartment: [],
            jsonFilteredData: [],
            label: false,
            totalConsumedStorage: 0,
            totalAllowedStorage: 0,
            NoRecordLength: 0,
            isDeptSearchOn: false,
            isDesgSearchOn: false,
            arrayDepartmentChecks: [],
            departmentSelectedArray: [],
            designationSelectedArray: [],
            arrayDesignationChecks: [],
            CheckIfExistDesignation: [],
            CheckIfExist: [],
            txtDepartmentName: [],
            tempArray: [],
            txtSearchDeptValue: '',
            txtSearchDesgValue: '',
            droplabeldept: true,
            droplabeldesg: false,
            FinalCheckedArray: [],
            unCheckedArray: [],
            DepartmentDataTemp:[],
            labelChecked: false,
            desglabelChecked:false,
            DesignationDataTemp:[],
            isShowDept: false,
            isShowDesg:false,
            avaliablestorage:null,
            AllowedClientStorage:null,
            leftSearchfeild:schema.lefrsearchschema,
            tempUserData:[],
            jsonObjData:[]
        }
    },

    created() {

        var vm = this;
        vm.FetchData();
        vm.FetchClientData();
         schema.bindleftCommonSearchdropdown(vm,"DocStorage","DEPARTMENT");
         schema.bindleftCommonSearchdropdown(vm,"DocStorage","Designation");
    },



    methods:
    {

        DisplaySelectedArray:function(DisplayType)
        {
            var vm = this;
            
            if(DisplayType == 'Dept')
            {
                if(vm.departmentSelectedArray.length>0)
            {
                vm.isShowDept = true;
                
            }
            else if(vm.departmentSelectedArray.length<0){
                vm.isShowDept = false;
            }
            }

           else if(DisplayType == 'Desg')
            {
                if(vm.designationSelectedArray.length>0)
            {
                vm.isShowDesg = true;
                
            }
            else if(vm.designationSelectedArray.length<0){
                vm.isShowDesg = false;
               
            }
            }
            
        },

        

       SelectAllOnClick: function(keyType){
        
        var vm= this;

        if(keyType == "Dept")
        {    
        if(vm.labelChecked == false)
        {

            $.each(vm.DepartmentData, function (index, item){
              //  $('#DisableDeptCheck' + item.value).prop("checked", true)                      
                                     item.checkitem = true;     
                                     vm.isDeptSearchOn = true;                                    
                                     vm.enableLeftItem(item,keyType);
                             });


                             vm.labelChecked = true;
        }
       else
        {
            $.each(vm.DepartmentData, function (index, item) {
              //  $('#DisableDeptCheck' + item.value).prop("checked", false)                          
                                     item.checkitem = false;  
                                     vm.departmentSelectedArray=[];
                                     vm.isDeptSearchOn = false;
                                     vm.isShowDept = false;
                             });
                            // vm.labelChecked = false;
        }
        
        if( ! $('.selectallId').prop("checked")  == false)
        {
            
            vm.departmentSelectedArray=[];
            $('.selectallId').prop("checked", true);
            $.each(vm.DepartmentData, function (index, item){
                item.checkitem = true;
                vm.enableLeftItem(item,keyType);
                
            })
        }
           
    }

    if(keyType == "Desg")
    {
        if(vm.desglabelChecked == false)
        {

            $.each(vm.designationData, function (index, item) {
                                   
                                     item.checkitem = true;     
                                     vm.isDesgSearchOn = true; 
                                     vm.enableLeftItem(item,keyType);
                             });
                             vm.desglabelChecked = true;
        }
        else 
        {
            $.each(vm.designationData, function (index, item) {
                               
                                     item.checkitem = false;  
                                     vm.designationSelectedArray=[];
                                     vm.isDesgSearchOn = false;
                                     vm.isShowDesg = false;
                             });
                           //  vm.desglabelChecked = false;
        }

        if( ! $('.selectallDesgId').prop("checked")  == false)
        {
           
            vm.designationSelectedArray=[];
            $('.selectallDesgId').prop("checked", true);
            $.each(vm.designationData, function (index, item){
                item.checkitem = true;
                vm.enableLeftItem(item,keyType);
                
            })
        }
    
    }
       },

      
        ClearFilter: function ( DDLIds, ddlLabel) {
            debugger
            var vm = this;
          
            if (ddlLabel == 'departmentDDL') {
                
            
                vm.departmentSelectedArray = vm.departmentSelectedArray.filter(dept => dept.value != DDLIds)
                     $('.checkedcls' + DDLIds).prop("checked", false);
                  
                    
                     vm.DepartmentDataTemp.forEach((item) => {
                        if(item.value == DDLIds)
                        {
                            item.checkitem = false;
                        }
                     })
             
                     if(vm.departmentSelectedArray.length < vm.DepartmentData.length)
                     {
                        $('.selectallId').prop("checked", false);
                     }
                     
            }

            else if (ddlLabel == 'designationDDL') {
               
                vm.designationSelectedArray = vm.designationSelectedArray.filter(desig => desig.value != DDLIds)
                     $('.checkedDesgcls' + DDLIds).prop("checked", false);

                     vm.DesignationDataTemp.forEach((item) => {
                        if(item.value == DDLIds)
                        {
                            item.checkitem = false;
                        }
                     })

                     if(vm.designationSelectedArray.length < vm.designationData.length)
                     {
                        $('.selectallDesgId').prop("checked", false);
                     }
                
            }


            if (vm.departmentSelectedArray.length > 0) {
                vm.isDeptSearchOn = true;
            }
            else {
                vm.isDeptSearchOn = false;
            }

            if (vm.designationSelectedArray.length > 0) {
                vm.isDesgSearchOn = true;
            }
            else {
                vm.isDesgSearchOn = false;
            }


        },

        enableLeftItem: function (item, keyType) {
            
            var vm = this;
            // vm.isDeptSearchOn = false;
            // vm.isDesgSearchOn = false;
            if (keyType == 'Dept') {
                if (item.checkitem==true) {
                    vm.departmentSelectedArray = vm.departmentSelectedArray.concat(item);
                }
                else {
                    vm.departmentSelectedArray = vm.departmentSelectedArray.filter(dept => dept.value != item.value);
                    $('.checkedcls' + item.value).prop("checked", false);
                    vm.isShowDept = false;

                }
                if (vm.departmentSelectedArray.length > 0) {
                    vm.isDeptSearchOn = true;
                }
                else {
                    vm.isDeptSearchOn = false;
                }
                 if (vm.departmentSelectedArray.length == vm.DepartmentData.length)
                     {
                        $('.selectallId').prop("checked", true);
                     }

                     if (vm.departmentSelectedArray.length < vm.DepartmentData.length)
                     {
                        $('.selectallId').prop("checked", false);
                     }
                
            }
            else if (keyType == 'Desg') {
                if (item.checkitem==true) {
                    vm.designationSelectedArray = vm.designationSelectedArray.concat(item);
                }
                else {
                    vm.designationSelectedArray = vm.designationSelectedArray.filter(desig => desig.value != item.value);
                    $('.checkedDesgcls' + item.value).prop("checked", false);
                    vm.isShowDesg = false;
                }
                if (vm.designationSelectedArray.length > 0) {
                    vm.isDesgSearchOn = true;
                }
                else {
                    vm.isDesgSearchOn = false;
                }
                if (vm.designationSelectedArray.length == vm.designationData.length)
                     {
                        $('.selectallDesgId').prop("checked", true);
                     }

                     if (vm.designationSelectedArray.length < vm.designationData.length)
                     {
                        $('.selectallDesgId').prop("checked", false);
                     }
               
            }

        },
        isNumber: function (evt) {
                    evt = evt ? evt : window.event;
                    var charCode = evt.which ? evt.which : evt.keyCode;
                    if (
                        charCode > 31 &&
                        (charCode < 48 || charCode > 57) &&
                        charCode !== 46
                    ) {
                        evt.preventDefault();
                    } else {
                        return true;
                    }
                    },
        disableButton: function (disablekey) {
            var vm = this;
            if (disablekey == 'Dept') {
                if(vm.departmentSelectedArray.length>0)
                {

                    vm.isDeptSearchOn = true;
                }
                else{
                    vm.isDeptSearchOn = false;
                }
            }
            else if (disablekey == 'Desg') {
                if(vm.designationSelectedArray.length>0)
                {
                    vm.isDesgSearchOn = true;

                }
                else
                {
                    vm.isDesgSearchOn = false;
                }
            }
        },

        SearchUserStorage: function () {
            var vm = this;
            vm.userFilterData = [];
             vm.JSONSearchData=[];
            $('input[name=Department]:checked').each(function () {
                var deptId = $(this).val();
                vm.CheckIfExistDepartment = vm.UserData.filter(dept => dept.DEPARTMENT_ID == deptId)
                vm.JSONSearchData = Array.from(vm.CheckIfExistDepartment);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
                if (vm.departmentSelectedArray.length > 0) {
                    vm.dropDivShow = true;
                }
                else {
                    vm.dropDivShow = false;
                }

            });

            $('input[name=Designation]:checked').each(function () {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                var designId = $(this).val();
                vm.CheckIfExistDesignation = vm.UserData.filter(desig => desig.DESIGNATION_ID == designId);
                vm.JSONSearchData = Array.from(vm.CheckIfExistDesignation);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
                if (vm.designationSelectedArray.length > 0) {
                    vm.divDesignationShow = true;
                }
                else {
                    vm.divDesignationShow = false;
                }

            });
            if ($("input[name=UserName]").val() != "") {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                vm.CheckIfExist = vm.jsonFilteredData.filter(usr => usr.USER_NAME.includes($("input[name=UserName]").val()));
                vm.JSONSearchData = Array.from(vm.CheckIfExist);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
            }
            if ($("input[name=ConsumedStorageFrom]").val() != "") {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                vm.CheckIfExist = vm.jsonFilteredData.filter(function (el) {
                     if(parseInt($("input[name=ConsumedStorageFrom]").val())<=el.USED_STORAGE && parseInt($("input[name=ConsumedStorageTo]").val())>=el.USED_STORAGE){
                        return el.USED_STORAGE;
                     }
                    
                });
                vm.JSONSearchData = Array.from(vm.CheckIfExist);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
            }
            if($("input[name=ConsumedStorageFrom]").val()=="" && $("input[name=ConsumedStorageTo]").val()=="" && $("input[name=UserName]").val() == "" &&  $('input[name=Department]:checked').length==0 &&  $('input[name=Designation]:checked').length==0)
            {
             vm.UserData= vm.tempUserData;
            }
            else 
            {
                vm.UserData = vm.userFilterData;
                //vm.FetchData();
            }
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            vm.getTotalStorageData();
        },


        ResetButton: function () {
            
            var vm = this;

            vm.FetchData();
            vm.GetDDLDepartmentData();
            vm.GetDDLDesignationData();
            $('#deptToggle').hide();
            $('#desgToggle').hide();
            vm.showByNameDiv = false;
            vm.showByConsumedDiv = false;
            $('.DepartmentchkItems').prop("checked", false);
            $('.DesignationchkItems').prop("checked", false);
            vm.txtSearchUserName = '';
            vm.txtConsumedStorage = '';
            vm.toggleExpandCollapseButton(vm.label = true);
            vm.userFilterData = [];
            vm.designationSelectedArray = [];
            vm.departmentSelectedArray = [];
            vm.txtSearchDeptValue = '';
            vm.txtSearchDesgValue = '';
            vm.isDeptSearchOn = false;
            vm.isDesgSearchOn = false;
            vm.isShowDesg = false;
            vm.isShowDept = false;
            $('.selectallId').prop("checked", false);
            $('.selectallDesgId').prop("checked", false);

           // window.location.reload();


        },

        toggleExpandCollapseButton: function () {
            var vm = this;
          
            if (vm.label == false) {
                $('#collapseUserName').show();
                $('#desgToggle').show();
                vm.showByNameDiv = true;
                vm.showByConsumedDiv = true;
                vm.label = true;
                vm.isDeptSearchOn = true;
                vm.isDesgSearchOn = true;
            }
            else {
                $('#collapseUserName').hide();
                $('#desgToggle').hide();
                vm.showByNameDiv = false;
                vm.showByConsumedDiv = false;
                vm.label = false;
                vm.isDeptSearchOn = true;
                vm.isDesgSearchOn = true;

            }


            if (vm.departmentSelectedArray.length > 0) {
                vm.isDeptSearchOn = true;
            }
            else {
                vm.isDeptSearchOn = false;
            }

            if (vm.designationSelectedArray.length > 0) {
                vm.isDesgSearchOn = true;
            }
            else {
                vm.isDesgSearchOn = false;
            }


        },

        getTotalStorageData: function () {
            var vm = this;
            vm.totalAllowedStorage = 0;
            vm.totalConsumedStorage = 0;
            vm.UserData.forEach(x => {
                if(x.STATUS_ID=='1001'){
                vm.totalAllowedStorage += x.ALLOWED_STORAGE;
                vm.totalConsumedStorage += x.USED_STORAGE;
                }
             
            });
            vm.avaliablestorage=parseInt(vm.totalAllowedStorage)-parseInt(vm.totalConsumedStorage )

        },

        AllocatedSpacePopup: function () {
            var vm = this;
            vm.jsonobj = []
             var id='';
            var values = ''
            $('#tablelistingdata .chkItems:checked').not("[id^='chkAll']").each(function () {

                values = $(this).closest('tr').find('.maxAllowedStorage').val();
                id=$(this).val();
                 let objData = {
                    Id: id,
                    value:values
                }
                vm.jsonobj.push(objData)
            })
            vm.isAllocatedSpaceOpen = true;
        },

        closeAllocatedSpace: function () {
            var vm = this;
            vm.isAllocatedSpaceOpen = false;
        },

        // Save data on save icon
        SaveData: function (id, allowed) {
            var vm = this
            vm.isLoading = true;
            vm.UserId = id,
                vm.UserStorageValue = allowed;
            let objData = {

                DataId:  vm.UserId,
                DataValue: vm.UserStorageValue
            }
            vm.jsonObjData.push(objData)
            let userSetting=[{
                    refCode: 'UserSetting',
                    moduleName: 'DocStorage',
                }]
            var configData="configData"
             var conditionData="conditionData"
            var params="[{"+JSON.stringify(configData)+":"+JSON.stringify(vm.jsonObjData)+", "+JSON.stringify(conditionData)+":"+JSON.stringify(userSetting)+"}]"
            DataService.ManageDocUserStorageSettingData(JSON.stringify(params)).then((response) => {

                if (response.data == 0) {
                    this.ShowAlert("Storage Configuration Data Updated Successfully")
                    $('#idMaxStorage' + id).attr('disabled','disabled');
                    $('#divAction' + id).hide();
                    $('#idEdit' + id).show();
                    vm.FetchData();
                    vm.GetDDLDesignationData();
                    vm.GetDDLDepartmentData();
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            })
            vm.isLoading = false;
        },

        CancelAction: function (id) {
            var vm = this;
            $('#idMaxStorage' + id).attr('disabled',true);
            $('#idEdit' + id).show();
            $('#divAction' + id).hide();
            //vm.FetchData();
            vm.GetDDLDesignationData();
            vm.GetDDLDepartmentData();
        },

        editAllowedStorage: function (id) {
            var vm = this;
            $('#idMaxStorage' + id).removeAttr('disabled');
            $('#idEdit' + id).hide();
            $('#divAction' + id).show();

        },

        //Pagination
        PagerData: function (PageNo, sortBy, sortExp, pageSize) {
            //
            var vm = this;
            vm.pageNo = PageNo;
            vm.pageSize = pageSize;
            vm.FetchData(vm.pageNo, sortBy, sortExp, vm.pageSize);
        },

        // To pass variable
        pagerMethod: function (e) {
            ;
            if (e.target.innerHTML == 'Prev') {
                this.pageNo = this.pageNo - 1;
            }
            else if (e.target.innerHTML == 'Next') {
                this.pageNo = parseInt(this.pageNo) + 1;
            }
            else {
                this.pageNo = e.target.innerHTML;
            }
            if (this.pageNo == "First") {
                this.pageNo = "1";
            }
            if (this.pageNo == "Last") {
                this.pageNo = this.TotalPages
            }
            if (parseInt(this.pageNo) > 0) {
                this.PagerData(this.pageNo, '', '', this.pageSize)
            }
            else {
                this.pageNo = 1
            }
        },

        pageSizecommon: function (e) {

            var vm = this;
            vm.pageNo = 1;
            vm.pageSize = e.target.value
            vm.PagerData(vm.pageNo, '', '', vm.pageSize)
        },
        onChange: function () {
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#btnStorageSpace').removeClass('disabled');
            }
            else {
                $('#btnStorageSpace').addClass('disabled');
            }

        },

        ReflectedDepartmentData: function(event)
        {
            
            var vm = this;
          
            if (event.target.value != undefined) {
                vm.DepartmentDataTemp = vm.DepartmentData.filter(usr => usr.name.toLowerCase().includes(event.target.value));
            }
            
        },

        ReflectedDesignationData: function(event)
        {
            
            var vm = this;
          
            if (event.target.value != undefined) {
                vm.DesignationDataTemp = vm.designationData.filter(usr => usr.name.toLowerCase().includes(event.target.value));
            }
            
        },

        GetDDLDepartmentData: function () {
            
            var vm = this;
            let params = {};
            params["type"] = "DOC_TAG";
            var param = "moduleName=DocStorage&type=DEPARTMENT"
            DataService.GetDDLDatas(param).then((response) => {
                vm.DepartmentData = response.data.DATA;
                vm.DepartmentDataTemp=vm.DepartmentData;

                $.each(vm.DepartmentData, function (index, item) {
                                 item.checkitem = false;                            
                         });
						 

              


            })
        },

        GetDDLDesignationData: function () {
            var vm = this;
            let params = {};
            params["type"] = "DOC_TAG";
            var param = "moduleName=DocStorage&type=HR_DESIG"
            DataService.GetDDLDatas(param).then((response) => {
                vm.designationData = response.data.DATA;
                vm.DesignationDataTemp = vm.designationData;

                $.each(vm.designationData, function (index, item) {
                    item.checkitem = false;
                });
            })
        },
        SetLeftSearch: function(){
            var vm=this;
            vm.UserData= vm.tempUserData;
            vm.SearchUserStorage();
        },
        ExpandCollapseList() {
            var element1 = document.getElementById("UserLeftMenu");
            element1.classList.toggle("left-menu-tab-collapsed");
            var element2 = document.getElementById("UserRightContent");
            element2.classList.toggle("right-content-expand");
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            vm.UserData=[];
            var params = `pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userTypeIds=1,2,3`;
            DataService.GetUserStorageDetail(params).then(function (response) {

                vm.UserData = JSON.parse(response.data).USERSTORAGEDETAILLIST;
                vm.tempUserData=JSON.parse(response.data).USERSTORAGEDETAILLIST;
                vm.totalRecords = vm.UserData[0].totalRecords
                vm.userStorageDetailSummary = JSON.parse(response.data).USERSTORAGEDETAILSUMMARY;

                vm.getTotalStorageData();
              //  $('#deptToggle').hide();
               // $('#desgToggle').hide();
                vm.isLoading = false;
                if (vm.UserData == null) {
                    vm.NoRecordLength = 0;
                }
                else {
                    vm.NoRecordLength = vm.UserData.length;
                }
                setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 1000);
            })
           
            $('#btnStorageSpace').addClass('disabled')

        },
        FetchClientData: function () {
            
            var vm = this;
            vm.isLoading = true;
            var params = `pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userTypeIds=4`;
            DataService.GetUserStorageDetail(params).then(function (response) {
                 JSON.parse(response.data).USERSTORAGEDETAILLIST.forEach(x => {
                        if(x.STATUS_ID==1001 || x.STATUS_ID==1002){
                                if(x.ALLOWED_STORAGE==null||x.ALLOWED_STORAGE==undefined){
                        x.ALLOWED_STORAGE=0;
                    vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                    }
                    else{
                        vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                    }
                        }
                })
               
            })
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            $('#btnStorageSpace').addClass('disabled')

        },
    }
}

</script>

<style>
.scrolll {
    height: 300px;
    overflow: scroll;
}
</style>